nav {
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    top: 0px;
    left: 0px;
    width: calc(100vw - 40px);
    height: 47px;
}

.left, .right{
    display: flex;
    align-items: center;
}

.left, .right {
    flex-grow: 0.5;
    flex-basis: 0;
 }

.right {
    display: flex;
    justify-content: flex-end;
 }

.spacer{
    height: 10px;
    width: 10px;
}

.separator {
    height: 18px;
    width: 2px;
    border-radius: 9px;
    background-color: white;
    opacity: 20%;
}

.nameSVG svg {
    filter: invert();
    position: relative;
    top: 2px;
    height: 25px;
    width: auto;
}


.pfp-nav-img {
    border-radius: 100%;
    max-width: 40px;
}

.pfp-nav-ring-2 {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 43px;
    height: 43px;
    border-radius: 160px;
    background-color: var(--bg);
}

.pfp-nav-ring-1 {

    display: flex;
    align-items: center;
    justify-content: center;
    width: 47px;
    height: 47px;
    border-radius: 160px;
    background-color: rgb(117, 117, 255);
}