.socialBtn {
    color: white;
    margin-left: 5px;
    margin-right: 5px;
    display: inline-block;
    position: relative;
}

.socialBtn {
    opacity: 50%;
    font-size: 150%;
    transition: opacity 0.4s;
    transition: font-size 0.2s
}

.socialBtn:hover {
    opacity: 100%;
    font-size: 200%;
}