:root {
  --bg: #000;
  --text-color: #fff;
}

html,
body {
  background-color: var(--bg);
}

.content{
  position: relative;
  bottom: 47px;
}

h2 {
  font-family: 'Tsukimi Rounded', serif;
  color: var(--text-color);
}

#title {
  font-weight: 500;
  position: absolute;
  top: calc(50vh - 150px);
  left: 50vw;
  transform: translate(-50%, -50%);
}

#main-intro {
  width: 100%;
  text-align: center;
  font-weight: 700;
  position: absolute;
  top: calc(50vh + 100px);
  left: 50vw;
  transform: translate(-50%, -50%);
}

#sub-intro {
  font-weight: 500;
  filter: opacity(0.7);
  font-size: medium;
  position: absolute;
  top: calc(50vh + 135px);
  left: 50vw;
  transform: translate(-50%, -50%);
  animation-delay: 2s;
}

.h-first {
  opacity: 0%;
  animation-name: fadehfin;
  animation-duration: 2s;
  animation-delay: 1.5s;
  animation-fill-mode: forwards;
}

@keyframes fadehfin {
  from {
    opacity: 0%;
  }

  to {
    opacity: 100%;
  }
}

.pfp-img {
  position: absolute;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
  border-radius: 100%;
  max-width: 150px;
}

.pfp-ring-2 {
  position: absolute;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
  width: 160px;
  height: 160px;
  border-radius: 160px;
  background-color: var(--bg);
}

.pfp-ring-1 {
  position: absolute;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
  width: 165px;
  height: 165px;
  border-radius: 160px;
  background-color: rgb(117, 117, 255);
}

.name-hw {
  position: absolute;
  top: calc(50vh + 50px);
  left: 50vw;
  transform: translate(-50%, -50%);
  width: 300px;
  filter: invert();
}

.name-hw #canvas {
  width: 300px;
}

