.navBtn {
    color: white;
    margin-left: 5px;
    margin-right: 5px;
    display: inline-block;
    position: relative;
}

.inactive {
    opacity: 50%;
    font-size: 90%;
    transition: opacity 0.4s;
    transition: font-size 0.2s
}

.inactive:hover {
    opacity: 100%;
    font-size: 130%;
}

span {
    margin-left: 4px;
    font-family: 'Tsukimi Rounded', serif;
    font-weight: 700;
}

i{  
    font-style: normal;
    position: relative;
    bottom: 1px;
}

