.navBtn.inactive::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  background: white;
  bottom: -5px;
  left: 0;
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.2s ease-out;
  border-radius: 5px;
}

.navBtn:hover::after,
.navBtn:hover::before {
  transform: scaleX(1);
}